.sidebar {
    flex: 1;
    border-right: 0.5px solid rgb(231, 228, 228);
    min-height: 100vh;
    background-color: white;

    .topT {
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center;

        .logo {
            font-size: 20px;
            font-weight: bold;
            color: #6439ff;
        }
    }

    hr {
        height: 0;
        border: 0.5px solid rgb(230, 227, 227);
    }

    .center {
        padding-left: 10px;

        ul {
            list-style: none;
            padding: 0;
            margin: 0;

            .title {
                font-size: 10px;
                font-weight: bold;
                color: #999;
                margin: 15px 0;
            }
            li {
                display: flex;
                align-items: center;
                padding: 5px;
                cursor: pointer;

                &:hover {
                    background-color: #ece8ff;
                }

                .icon {
                    font-size: 18px;
                    color: #7451f8;
                }

                span {
                    font-size: 13px;
                    font-weight: bold;
                    color: #888;
                    margin-left: 10px;
                }
            }
        }
    }

    .bottomT {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 10px;

        .colorOption {
            width: 20px;
            height: 20px;
            border-radius: 5px;
            border: 1px solid #7451f8;
            cursor: pointer;
            margin: 5px;

            &:nth-child(1) {
                background-color: whiteSmoke;
            }
            &:nth-child(2) {
                background-color: darkblue;
                
            }
        }


    }
}